export default {
  name: "default",
  data() {
    return {
      activeIndex: 0,
      itemStates: [{
        isNotChosen: true,
        isChosen: false
      }, {
        isNotChosen: true,
        isChosen: false
      }, {
        isNotChosen: true,
        isChosen: false
      }, {
        isNotChosen: true,
        isChosen: false
      }]
    };
  },
  methods: {
    choose(index) {
      this.activeIndex = index;
      this.itemStates.forEach((state, i) => {
        state.isNotChosen = i !== index;
        state.isChosen = i === index;
      });
    },
    chooseMid() {
      this.activeIndex = -1;
      this.itemStates.forEach(state => {
        state.isNotChosen = true;
        state.isChosen = false;
      });
    }
  }
};